<template>
  <div class="accordion" :class="{className, 'accordion--columns': columns }">
    <AccordionAtom
        v-for="( item, index) in modifiedList"
        :key="'accordion-item-' + index"
        :item="item"
        :index="index"
        :columns="columns"
        @changeList="changeList"
    />
  </div>

</template>

<script>
import AccordionAtom from "@/atoms/AccordionAtom";

export default {
  data() {
    return {
      listCopy: [...this.list] //    ,        
    };
  },
  props: {
    list: {
      type: Array,
      required: true
    },
    columns: {
      type: Boolean,
      default: false
    },
    multiple: {
      type: Boolean,
      default: false
    },
    className: {
      type: String
    }
  },
  components: {
    AccordionAtom,
  },
  methods: {
    changeList(i) {
      //  ,  ,        ,     ,   

      /* this.listCopy = this.listCopy.map((item, index) => ({
         ...item,
         isActive: i === index ? !item.isActive : false
      }));
         multiple
       */

      //  multiple false,        isActive false
      if (!this.multiple) {
        this.listCopy.forEach((item) => {
          item.isActive = false;
        });
      }

      //         ,   isActive
      this.listCopy.splice(i, 1, { ...this.listCopy[i], isActive: !this.listCopy[i].isActive });
    }
  },
  computed: {
    modifiedList() {
      //        
      return this.listCopy.map(item => ({...item}));
    }
  }
}
</script>

<style scoped lang="scss">
.accordion {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.accordion--columns {
  flex-direction: row;
  gap: 10px 40px;
  flex-wrap: wrap;
}

.column {
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
  width: calc(50% - 20px);
}
</style>
